
export const Phonebook = ({person, handleDelButton, key}) => { 
  const onClick = (person) => {
    handleDelButton(person);
  }
  return(
    <div>
      {person.name} {person.number} 
      <button value={person.name} data-mssg={person.id} onClick={onClick(person)}>delete</button>
    </div>
  )

  }